.priority {
    margin-top: 10px;
    width: 100%;
}
.priority-text {
    font-size: 1.5em;
    margin-top: 10px;
}
.priority-line {
    position: relative;
    width: 95%;
    margin: 30px auto 50px auto;
    border-radius: 8px;
    height: 12px;
    background: -webkit-gradient(linear , left top, right top , from(rgba(208, 72, 58, 0.9)) , color-stop(#Fecc35), color-stop(yellow), color-stop(#3de98f), to(#4b9c61));
    background: -webkit-linear-gradient(left , rgba(208, 72, 58, 0.9) , #Fecc35, yellow, #3de98f, #4b9c61);
    background: -o-linear-gradient(left , rgba(208, 72, 58, 0.9) , #Fecc35, yellow, #3de98f, #4b9c61);
    background: linear-gradient(to right , rgba(208, 72, 58, 0.9) , #Fecc35, yellow, #3de98f, #4b9c61);
}
.priority-choices0,
.priority-choices1,
.priority-choices2 {
    position: absolute;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background: white;
}
.priority-choices0 {
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    left: 18px;
    border: 1px solid rgba(208, 72, 58, 0.9);
}
.priority-choices1 {
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    border: 1px solid #fef568;
}
.priority-choices2 {
    top: 50%;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    right: 18px;
    border: 1px solid #4b9c61;
}
.active.priority-choices0:after,
.active.priority-choices1:after,
.active.priority-choices2:after {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
        -ms-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    width: 160%;
    height: 160%;
    background: transparent;
    border-radius: 50%;
    border: 2px solid black;
    border-left: none;
    border-right: none;
    z-index: -1;
}


