.ti {
    width: 90%;
    height: 523px;
    background: #FAFAFA;
    margin: 0 auto;
    overflow: scroll;
}

.ti ul {
    padding: 0;
    margin: 0;
    list-style: none;
    height: 100%;
}

.ti-item {
    margin-top: 20px;
    border-bottom: 2px solid rgba(179, 171, 171,0.2);
    color: black;
    text-transform: capitalize;
    border-radius: 4px;
    background: -webkit-gradient(linear, left top, right bottom, from(rgba(255, 255, 255, 0.4)), to(rgba(203, 203, 203, 0.4)));
    background: -webkit-linear-gradient(top left, rgba(255, 255, 255, 0.4), rgba(203, 203, 203, 0.4));
    background: -o-linear-gradient(top left, rgba(255, 255, 255, 0.4), rgba(203, 203, 203, 0.4));
    background: linear-gradient(to bottom right, rgba(255, 255, 255, 0.4), rgba(203, 203, 203, 0.4));
    -webkit-box-shadow: 0px 1px 3px rgba(203, 203, 203, 0.4);
            box-shadow: 0px 1px 3px rgba(203, 203, 203, 0.4);
    position: relative;
    height: 80px;
    overflow: hidden;
    cursor: pointer;
}
.ti-item-content {
    margin-left: 10px;
}
.ti-item-content:before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    width: 66px;
    height: 311px;
    -webkit-transform: rotateZ(-32deg) translateY(-50px);
        -ms-transform: rotate(-32deg) translateY(-50px);
            transform: rotateZ(-32deg) translateY(-50px);
    z-index: 1;
}
.ti-item-content.red:before {
    background: rgba(208, 72, 58, 0.9);
}
.ti-item-content.yellow:before {
    background: #fef568;
}
.ti-item-content.green:before {
    background: #4b9c61;
}
.ti-name {
    display: inline-block;
}
.ti-name h1 {
    font-size: 1.3em;
    margin: 0;
    font-weight: bold;
}
.ti-time-info .ti-blank-space {
    margin: 5px;
}
.ti-star {
    cursor: pointer;
}
@media screen and (max-width: 320px){
    .ti-item-content:before {
        -webkit-transform: rotateZ(-32deg) translateY(-50px) translateX(25px);
            -ms-transform: rotate(-32deg) translateY(-50px) translateX(25px);
                transform: rotateZ(-32deg) translateY(-50px) translateX(25px);
    }
}
.ti-star::before {
    content: '\2606';
    font-size: 25px;
    color: #b950b1;
}
.active.ti-star::before {
    content: '\2605';
    font-size: 25px;
    color: #b950b1;
}



